import uniList from '@/components/uni-list/uni-list.vue'
import uniListItem from '@/components/uni-list-item/uni-list-item.vue'
import cuPanel from '@/components/custom/cu-panel.vue'
import cuCell from '@/components/custom/cu-cell.vue'
import cuDate from '@/components/custom/cu-date.vue'
import { api } from '@/config/common.js'
import { queryReceive, queryPay } from '@/api/data.js'
import { dateFormat, numberFormat } from '@/utils/tools.js'
export default {
	components: {
		uniList,
		uniListItem,
		cuPanel,
		cuCell,
		cuDate
	},
	data() {
		return {
			pageType: 1,
			title: '',
			totalTitle1: '',
			totalTitle2: '',
			tableHeader1: '',
			tableHeader2: '',
			datas: {},
			initSort: [
				{ id: 0, name: '金额' },
				{ id: 1, name: '帐龄' }
			],
			initFilter: [
				{ id: 0, name: '所有', checked: true, condition: false },
				{ id: 1, name: '客户', checked: false, condition: true }
			],
			filter: 0,
			customerName: '',
			orderBy: 0
		}
	},
	onLoad(options) {
		this.pageType = parseInt(options.pageType)
		switch(this.pageType) {
			case 1:
				this.title = '应收分析'
				this.totalTitle1 = '欠款客户数'
				this.totalTitle2 = '应收金额'
				this.tableHeader1 = '账龄'
				this.tableHeader2 = '应收总金额'
				break
			case 2:
				this.title = '应付分析'
				this.totalTitle1 = '供应商欠款'
				this.totalTitle2 = '总应付'
				this.tableHeader1 = '账龄'
				this.tableHeader2 = '应付总金额'
				break
		}
		this.getData()
	},
	computed: {
		headerHeight() {
			return this.$headerHeight
		}
	},
	methods: {
		handleNavbarClickLeft() {
			uni.navigateBack({
				delta: 1
			})
		},
		handleNavTo(url, params) {
			uni.navigateTo({
				url: url+'?'+params
			})
		},
		getData() {
			let	reqData = {
				customerName: this.customerName,
				orderBy: this.orderBy
			}
			this.$refs.loading.open()
			switch(this.pageType) {
				case 1:
					queryReceive(api.report, reqData).then(res => {
						this.$refs.loading.close()
						if (res.status == 200 && res.data.returnCode == '0000') {
							this.datas = res.data.data
							this.customerName = ''
						}
					}).catch(error => {
						this.$refs.loading.close()
					})
					break
				case 2:
					queryPay(api.report, reqData).then(res => {
						this.$refs.loading.close()
						if (res.status == 200 && res.data.returnCode == '0000') {
							this.datas = res.data.data
							this.customerName = ''
						}
					}).catch(error => {
						this.$refs.loading.close()
					})
					break
			}
		},
		numberFilter(number) {
			return numberFormat(number)
		},
		compare(prop, type) {
			if (type == 'up') {
				return (obj1, obj2) => {
					let val1 = obj1[prop]
					let val2 = obj2[prop]
					if (val1 < val2) {
						return -1
					} else if (val1 > val2) {
						return 1
					} else {
						return 0
					}
				}
			} else {
				return (obj1, obj2) => {
					let val1 = obj1[prop]
					let val2 = obj2[prop]
					if (val1 > val2) {
						return -1
					} else if (val1 < val2) {
						return 1
					} else {
						return 0
					}
				}
			}
		},
		sortData(type, val) {
			this.initSort = this.initSort.map((item)=>{
				if (item.id == val) {
					item.checked = true
				} else {
					item.checked = false
				}
				return item
			})
			if (type == 'up') {
				if (val == 0) {
					this.datas.reportDetailQueries.reportReceiveAddPayCustomer.sort(this.compare('totalReceivableY', 'up'))
				} else {
					this.datas.reportDetailQueries.reportReceiveAddPayCustomer.sort(this.compare('maxReceiveAge', 'up'))
				}
			} else {
				if (val == 0) {
					this.datas.reportDetailQueries.reportReceiveAddPayCustomer.sort(this.compare('totalReceivableY', 'down'))
				} else {
					this.datas.reportDetailQueries.reportReceiveAddPayCustomer.sort(this.compare('maxReceiveAge', 'down'))
				}
			}
		},
		handleSortUp(val) {
			this.sortData('up', val)
		},
		handleSortDown(val) {
			this.sortData('down', val)
		},
		handleFilterOk(val) {
			this.initFilter = this.initFilter.map((item)=>{
				if (item.id == val) {
					item.checked = true
				} else {
					item.checked = false
				}
				return item
			})
			this.filter = val
			this.getData()
		},
		handleFilterCancel() {
			this.cutomerName = ''
		}
	}
}
